@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nova+Flat&display=swap");
body {
  background-color: #2f1847;
  font-family: "Poppins", sans-serif;
}

.btn,
.submitSuggestion {
  background-color: #a4b0f5;
  padding: 1.2vw 1.8vw;
  border-radius: 2.4vw 2.4vw;
  border: 0px;
  font-size: 16px;
  font-weight: bold;
  margin: 7.5px;
  margin-top: 5px;
  transition: padding 1s;
  animation-play-state: running;
  animation-name: scaleDown;
  animation-duration: 1s;
  -webkit-animation: 1600ms scaleDown normal alternate ease-out;
  text-align: center;
  outline-color: #8e7dbe;
  outline-width: 50px;
  border-style: hidden;
  color: #222725;
}

.btn:hover,
.untestedBtn:hover,
.submitSuggestion:hover {
  animation-play-state: running;
  animation-name: scaleUp;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  background-color: #8e7dbe;
}

.btn:active,
.untestedBtn:active,
.submitSuggestion:active {
  animation-play-state: paused;
  padding: 0.8vw 1.8vw;
  border-style: solid;
  border-width: 2px;
  border-color: #f0ebd8;
}

.untestedBtn {
  background-color: #a4b0f5;
  padding: 1.2vw 1.8vw;
  border-radius: 2.4vw 2.4vw;
  border: 0px;
  font-size: 16px;
  font-weight: bold;
  margin: 7.5px;
  margin-top: 5px;
  transition: padding 1s;
  animation-play-state: running;
  animation-name: scaleDown;
  animation-duration: 1s;
  -webkit-animation: 1600ms scaleDown normal alternate ease-out;
  text-align: center;
  outline-color: #8e7dbe;
  outline-width: 50px;
  border-style: hidden;
  color: #f03a47;
}

@keyframes scaleUp {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.1);
  }
}

@keyframes scaleDown {
  from {
    transform: scale(1.1);
  }
  to {
    transform: scale(1);
  }
}

.container {
  text-align: center;
  margin-top: 100px;
}

.footer {
  bottom: 0%;
  text-align: end;
  text-align: center;
  color: #8d99ae;
  position: fixed;
  bottom: 0%;
  left: 25%;
  right: 25%;
  /* margin-top: 34vw; */
}
a {
  color: #ead3ff;
  -webkit-animation: glow 1s ease-in-out infinite alternate;
  -moz-animation: glow 1s ease-in-out infinite alternate;
  animation: glow 1s ease-in-out infinite alternate;
  font-family: "Nova Flat", cursive;
  font-size: 26px;
  /* animation: pulsate 2.5s infinite alternate; */
}

.suggester {
  text-align: end;
  animation-name: none;
  font-size: 15px;
  position: fixed;
  text-align: center;
  left: 25%;
  right: 25%;
  margin-top: 0.5vw;
}

.header {
  color: #ffffff;
  font-size: 60px;
  animation-name: slideIn;
  animation-duration: 1s;
  animation-timing-function: ease-in-out;
  -webkit-animation-timing-function: ease-in-out;
}

.suggestHeader {
  color: white;
  text-align: center;
  font-size: 50px;
}

.suggestionField {
  color: #000000;
  /* padding: 6% 12%; */
  background-color: #f5f2e7;
  border: #ffffff;
  border-radius: 10px;
  text-align: start;
  width: 24vw;
  height: 12vw;
  position: fixed;
  left: 38%;
  right: 38%;
  outline: none;
  margin-top: 14vw;
}

.normalField {
  border-radius: 15px;
  background-color: #f5f2e7;
  border: #030303;
  outline: none;
  position: fixed;
  width: 12vw;
  height: 2vw;
  left: 44vw;
  text-align: center;
}

.submitSuggestion {
  position: fixed;
  left: 45.7vw;
  margin-top: 29vw;
}

.status {
  color: white;
  text-align: center;
  margin-top: 38vw;
}

#passField {
  margin-top: 9vw;
}

#lessonField {
  margin-top: 3vw;
}

#codeField {
  margin-top: 6vw;
}

@keyframes slideIn {
  from {
    margin-left: -100vw;
  }
  to {
    margin-left: 0px;
  }
}

@keyframes glow {
  from {
    text-shadow: 0 0 10px #bebdbd, 0 0 20px #bebdbd, 0 0 30px #8338ec,
      0 0 40px #8338ec, 0 0 50px #8338ec, 0 0 60px #8338ec, 0 0 70px #8338ec;
  }
  to {
    text-shadow: 0 0 20px #bebdbd, 0 0 30px #5e60ce, 0 0 40px #5e60ce,
      0 0 50px #5e60ce, 0 0 60px #5e60ce, 0 0 70px #5e60ce, 0 0 80px #5e60ce;
  }
}
@keyframes pulsate {
  100% {
    /* Larger blur radius */
    text-shadow: 0 0 4px #fff, 0 0 11px #fff, 0 0 19px #fff, 0 0 40px #b392e0,
      0 0 80px #b392e0, 0 0 90px #b392e0, 0 0 100px #b392e0, 0 0 150px #b392e0;
  }
  0% {
    /* Smaller blur radius */
    text-shadow: 0 0 2px #fff, 0 0 4px #fff, 0 0 6px #fff, 0 0 10px #b392e0,
      0 0 45px #b392e0, 0 0 55px #b392e0, 0 0 70px #b392e0, 0 0 80px #b392e0;
  }
}
